import React from "react";
import { Card, TextNodeV3, HTMLNodeV3 } from "../cards/Card";
import { TextV3 } from "../TypesV3";
import { LinkButton } from "../interactive/LinkButton";
import { ButtonGroup } from "../interactive/ButtonGroup";


/**
 * .
 */
export const BadUrl: React.FC = _props => (
    <div style={{
        height: "88vh",
        display: "flex",
        alignItems: "center",
        justifyContent: "center"
    }} >
        <Card>
            <header>
                <TextNodeV3>
                    {
                        new TextV3(
                            "404 - ",
                            undefined,
                            "404 - Bad GateWay"
                        )
                    }
                </TextNodeV3>
            </header>
            <p>
                <HTMLNodeV3>
                    {
                        new TextV3(
                            `<u>${ window.location.href }</u> `,
                            undefined,
                            `Could not get <u>${ window.location.href }</u>. `
                        )
                    }
                </HTMLNodeV3>
            </p>
            <ButtonGroup style={{
                padding: "20px"
            }} >
                <LinkButton to="/" >
                    {
                        new TextV3(
                            ``,
                            "",
                            `Home`
                        )
                    }
                </LinkButton>
            </ButtonGroup>
        </Card>
    </div>
);
