import React, { Component } from "react";
import { PageFlow } from "../containers/PageFlow";
import { WelcomeCard } from "../cards/WelcomeCard";
import { Card, TextNodeV3 } from "../cards/Card";
import { TextV3 } from "../TypesV3";
import { LinkButton } from "../interactive/LinkButton";


/**
 *  / .
 * 
 * @export
 * @class Home
 * @extends {Component<{}, {}>}
 */
export class Home extends Component {

    public constructor(props: {}) {
        super(props);
        this.state = {};
    }
    
    public render(): JSX.Element {
        return (
            <div>
                <PageFlow height="92vh" showTip={ true } >
                    <WelcomeCard />
                    <Card key="menu" >
                        <header>
                            <TextNodeV3>
                                {
                                    new TextV3(
                                        "",
                                        "",
                                        "EXPLORE"
                                    )
                                }
                            </TextNodeV3>
                        </header>
                        <div key="bg0"
                        style={{
                            display: "flex",
                            flexDirection: "column",
                            alignItems: "stretch",
                            justifyContent: "space-between"
                        }} >
                            <LinkButton key="paper" to="/paper"
                            style={{
                                width: "60%",
                                margin: "1em 0"
                            }} >
                                {
                                    new TextV3(
                                        "",
                                        "",
                                        "Papers"
                                    )
                                }
                            </LinkButton>
                            <LinkButton key="project" to="/project"
                            style={{
                                width: "60%",
                                margin: "1em 0"
                            }} >
                                {
                                    new TextV3(
                                        "",
                                        "",
                                        "Recent Work"
                                    )
                                }
                            </LinkButton>
                            <LinkButton key="article" to="/article"
                            style={{
                                width: "60%",
                                margin: "1em 0"
                            }} >
                                {
                                    new TextV3(
                                        "",
                                        "",
                                        "Articles"
                                    )
                                }
                            </LinkButton>
                            <LinkButton key="idea" to="/idea"
                            style={{
                                width: "60%",
                                margin: "1em 0"
                            }} >
                                {
                                    new TextV3(
                                        "",
                                        "",
                                        "Idea"
                                    )
                                }
                            </LinkButton>
                            <LinkButton key="storage" to="/storage" type="danger"
                            style={{
                                width: "60%",
                                margin: "1em 0"
                            }} >
                                {
                                    new TextV3(
                                        "",
                                        "",
                                        "Storage"
                                    )
                                }
                            </LinkButton>
                        </div>
                        <p style={{
                            marginTop: "100px"
                        }} >
                            <a href="/v2" style={{
                                color: "rgb(206,186,133)"
                            }} >
                                <TextNodeV3>
                                    {
                                        new TextV3(
                                            " 2.x ",
                                            "v2.x ",
                                            "Click here to move back to v2.x"
                                        )
                                    }
                                </TextNodeV3>
                            </a>
                        </p>
                    </Card>
                </PageFlow>
            </div>
        );
    }

    public componentDidMount(): void {}

};

